.dialogs {
    display: grid;
    grid-template-columns: 2fr 10fr;
}

.dialogs img {
    width: 50px;
    border-radius: 20px;
}

.dialogsItems {
    padding: 10px;
}

.dialogsItems .active {
    color: gold;
}

.dialog a {
    text-decoration: none;
}

.messages {
    padding: 10px;
}

.message {
    display: block;
    padding: 10px;
    background-color: aquamarine;
}